//@ts-nocheck
const ACTION_TYPE_ATTRIBUTE = 1; //åŠ¨ä½œç±»åž‹ ä¿®æ”¹å…ƒç´ å±žæ€§
const ACTION_TYPE_ELEMENT = 2; //åŠ¨ä½œç±»åž‹ å…ƒç´ å¢žå‡
const ACTION_TYPE_MOUSE = 3; //åŠ¨ä½œç±»åž‹ å…ƒç´ å¢žå‡

/**
 * domå’Œactionså¯JSON.stringify()åºåˆ—åŒ–åŽä¼ é€’åˆ°åŽå°
 */
function JSVideo() {
  this.id = 1;
  this.idMap = new Map(); //å”¯ä¸€æ ‡è¯†å’Œå…ƒç´ ä¹‹é—´çš„æ˜ å°„
  this.dom = this.serialization(document.documentElement);
  console.log("åºåˆ—åŒ–", this.dom);
  console.log("map", this.idMap);
  this.currentObserve = null;
  this.actions = []; //åŠ¨ä½œæ—¥å¿—
  this.mouseTimer = null; //é¼ æ ‡timer
  this.observer();
  this.observerInput();
  this.observerMouse();
}

JSVideo.prototype = {
  /**
   * DOMåºåˆ—åŒ–
   */
  serialization(parent) {
    if (parent.tagName === "SCRIPT") {
      return null;
    }
    let element = this.parseElement(parent);
    if (parent.children.length == 0) {
      parent.textContent && (element.textContent = parent.textContent);
      return element;
    }
    Array.from(parent.children, (child) => {
      element.children.push(this.serialization(child));
    });
    return element;
  },
  /**
   * å°†å…ƒç´ è§£æžæˆå¯åºåˆ—åŒ–çš„å¯¹è±¡
   */
  parseElement(element, id) {
    if (!element) {
      return null;
    }
    let attributes = {};
    for (const { name, value } of Array.from(element.attributes)) {
      attributes[name] = value;
    }
    if (!id) {
      //è§£æžæ–°å…ƒç´ æ‰åšæ˜ å°„
      id = this.getID();
      console.log("getID", element, id);
      this.idMap.set(element, id); //å…ƒç´ ä¸ºé”®ï¼ŒIDä¸ºå€¼
    }
    return {
      children: [],
      id: id,
      tagName: element.tagName.toLowerCase(),
      attributes: attributes,
    };
  },
  /**
   * DOMååºåˆ—åŒ–
   */
  deserialization(obj) {
    if (!obj) {
      return null;
    }
    let element = this.createElement(obj);
    if (obj.children.length == 0) {
      return element;
    }
    obj.children.forEach((child) => {
      const el = this.deserialization(child);
      if (el) {
        element.appendChild(el);
      }
    });
    return element;
  },
  /**
   * å°†å¯¹è±¡è§£æžæˆå…ƒç´ 
   */
  createElement(obj) {
    if (!obj) {
      return null;
    }
    let element = document.createElement(obj.tagName);
    if (obj.id) {
      this.idMap.set(obj.id, element); //IDä¸ºé”®ï¼Œå…ƒç´ ä¸ºå€¼
    }
    for (const name in obj.attributes) {
      element.setAttribute(name, obj.attributes[name]);
    }
    obj.textContent && (element.textContent = obj.textContent);
    return element;
  },
  /**
   * å”¯ä¸€æ ‡è¯†
   */
  getID() {
    return this.id++;
  },
  /**
   * åºåˆ—åŒ–åŽçš„DOM
   */
  getDOM() {
    return this.dom;
  },
  /**
   * ç›‘æŽ§å…ƒç´ å˜åŒ–
   */
  observer() {
    this.currentObserve = new MutationObserver((mutations) => {
      console.log(mutations);
      mutations.forEach((mutation) => {
        const { type, target, oldValue, attributeName, addedNodes, removedNodes } = mutation;
        console.log(type);

        switch (type) {
          case "attributes":
            const value = target.getAttribute(attributeName);
            console.log("attributes", value);
            this.setAttributeAction(target);
            break;
          case "childList":
            this.setAttributeAction(target, {
              type: ACTION_TYPE_ELEMENT,
              //æ–°å¢žçš„node ä¿å­˜çš„æ˜¯åºåˆ—åŒ–çš„dom
              addedNodes: Array.from(addedNodes, (el) =>
                this.serialization(el)
              ),
              //åˆ é™¤çš„node ä¿å­˜çš„æ˜¯node å¯¹åº”çš„id
              removedNodes: Array.from(removedNodes, (el) =>
                this.idMap.get(el)
              ),
            });
        }
      });
    });
    this.currentObserve.observe(document, {
      childList: true, // æ˜¯å¦è§‚å¯Ÿå­èŠ‚ç‚¹çš„å˜åŠ¨
      subtree: true, // æ˜¯å¦è§‚å¯Ÿæ‰€æœ‰åŽä»£èŠ‚ç‚¹çš„å˜åŠ¨
      attributes: true, // æ˜¯å¦è§‚å¯Ÿå±žæ€§çš„å˜åŠ¨
      attributeOldValue: true, // æ˜¯å¦è§‚å¯Ÿå±žæ€§çš„å˜åŠ¨çš„æ—§å€¼
      characterData: true, // æ˜¯å¦èŠ‚ç‚¹å†…å®¹æˆ–èŠ‚ç‚¹æ–‡æœ¬çš„å˜åŠ¨
      characterDataOldValue: true, // æ˜¯å¦èŠ‚ç‚¹å†…å®¹æˆ–èŠ‚ç‚¹æ–‡æœ¬çš„å˜åŠ¨çš„æ—§å€¼
      // attributeFilter: ['class', 'src'] åœ¨æ­¤æ•°ç»„ä¸­çš„å±žæ€§å˜åŒ–æ—¶å°†è¢«å¿½ç•¥
    });
    //this.currentObserve.disconnect();
  },
  // ç›‘å¬é¼ æ ‡
  observerMouse() {
    const mouse = document.createElement('div')
      mouse.className = 'app-mouse';
      doc.body.appendChild(mouse)
    this.mouseTimer = setInterval(() => {
      console.log('add ACTION_TYPE_MOUSE',);

      this.actions.push({
        type: ACTION_TYPE_MOUSE,
        timestamp: Date.now(),
        pageX: Math.round(Math.random() * 800),
        pageY: Math.round(Math.random() * 800)
      })
    }, 2000)
  },
  /**
   * ç›‘æŽ§æ–‡æœ¬æ¡†çš„å˜åŒ–
   */
  observerInput() {
    const original = Object.getOwnPropertyDescriptor(
      HTMLInputElement.prototype,
      "value"
    ),
      _this = this;
    //ç›‘æŽ§é€šè¿‡ä»£ç æ›´æ–°çš„valueå±žæ€§
    Object.defineProperty(HTMLInputElement.prototype, "value", {
      set(value) {
        console.log("defineProperty", value);
        setTimeout(() => {
          _this.setAttributeAction(this); //å¼‚æ­¥è°ƒç”¨ï¼Œé¿å…é˜»å¡žé¡µé¢
        }, 0);
        original.set.call(this, value); //æ‰§è¡ŒåŽŸæ¥çš„seté€»è¾‘
      },
    });
    //æ•èŽ·inputäº‹ä»¶
    document.addEventListener(
      "input",
      (event) => {
        const { target } = event;
        let text = target.value;
        console.log("input", text);
        this.setAttributeAction(target);
      },
      {
        capture: true, //æ•èŽ·
      }
    );
  },
  /**
   * é…ç½®ä¿®æ”¹å±žæ€§çš„åŠ¨ä½œ
   */
  setAttributeAction(element, otherParam = {}) {
    let attributes = {
      type: ACTION_TYPE_ATTRIBUTE,
      ...otherParam,
    };
    element.value && (attributes.value = element.value);
    console.log("setAttributeAction", attributes);
    this.setAction(element, attributes);
  },
  /**
   * é…ç½®ä¿®æ”¹åŠ¨ä½œ
   */
  setAction(element, otherParam = {}) {
    //ç”±äºŽelementæ˜¯å¯¹è±¡ï¼Œå› æ­¤Mapä¸­çš„keyä¼šè‡ªåŠ¨æ›´æ–°
    const id = this.idMap.get(element);
    console.log("idMap", "tagId", id, element);
    const action = Object.assign(
      this.parseElement(element, id),
      { timestamp: Date.now() },
      otherParam
    );
    this.actions.push(action);
    console.log("actions", this.actions);
    console.log("idMap", this.idMap);
  },
  getActions() {
    return this.actions;
  },
  /**
   * å›žæ”¾
   */
  replay() {
    if (this.actions.length == 0) return;
    console.log("new idMap", this.idMap);
    console.log("all actions", this.actions);

    const timeOffset = 16.7; //ä¸€å¸§çš„æ—¶é—´é—´éš”å¤§æ¦‚ä¸º16.7ms
    let startTime = this.actions[0].timestamp; //å¼€å§‹æ—¶é—´æˆ³
    const state = () => {
      const action = this.actions[0];
      console.log("action", action);

      let element = this.idMap.get(action.id);
      if (!element && action.type !== ACTION_TYPE_MOUSE) {
        //å–ä¸åˆ°çš„å…ƒç´  ä¸”ä¸æ˜¯é¼ æ ‡åŠ¨ä½œ ç›´æŽ¥åœæ­¢åŠ¨ç”»
        return;
      }
      //console.log("state action", action, this.actions.length);
      if (startTime >= action.timestamp) {
        this.actions.shift();
        switch (action.type) {
          //å±žæ€§
          case ACTION_TYPE_ATTRIBUTE:
            console.log("action>>>>>> attributes", action.id, element);
            for (const name in action.attributes) {
              //æ›´æ–°å±žæ€§
              element.setAttribute(name, action.attributes[name]);
            }
            //è§¦å‘definePropertyæ‹¦æˆªï¼Œæ‹†åˆ†æˆä¸¤ä¸ªæ’ä»¶ä¼šé¿å…è¯¥é—®é¢˜
            action.value && (element.value = action.value);
            break;

          //èŠ‚ç‚¹ä¿®æ”¹
          case ACTION_TYPE_ELEMENT:
            console.log("action>>>>>>> element", action.id, element);
            //æ·»åŠ èŠ‚ç‚¹
            action.addedNodes.forEach((ch) => {
              let el = this.createElement(ch);
              console.log('++æ·»åŠ èŠ‚ç‚¹', ch, el);
              element.appendChild(el);
            });
            //åˆ é™¤èŠ‚ç‚¹
            action.removedNodes.forEach((id) => {
              let el = this.idMap.get(id);
              console.log('--åˆ é™¤èŠ‚ç‚¹', id, el);
              element.removeChild(el);
            });
            break;

          //é¼ æ ‡
          case ACTION_TYPE_MOUSE:
            console.log("action>>>>>>> mouse", action);
            break;
        }
      }
      startTime += timeOffset; //æœ€å¤§ç¨‹åº¦çš„æ¨¡æ‹ŸçœŸå®žçš„æ—¶é—´å·®
      console.log(
        ">>>>>å‰©ä½™åŠ¨ä½œ:",
        this.actions.length,
        "tagId:",
        action.id,
        "startTime:",
        startTime
      );
      if (this.actions.length > 0) {
        //å½“è¿˜æœ‰åŠ¨ä½œæ—¶ï¼Œç»§ç»­è°ƒç”¨requestAnimationFrame()
        requestAnimationFrame(state);
      }
    };
    state();
  },
  /**
   * åˆ›å»ºiframeè¿˜åŽŸé¡µé¢
   */
  createIframe() {
    //åœæ­¢ç›‘å¬
    this.currentObserve.disconnect();
    window.clearInterval(this.mouseTimer)

    let iframe = document.createElement("iframe");
    iframe.setAttribute("sandbox", "allow-same-origin");
    iframe.setAttribute("scrolling", "no");
    iframe.setAttribute("style", "pointer-events:none; border:0;");
    iframe.width = `${window.innerWidth}px`;
    iframe.height = `${document.documentElement.scrollHeight}px`;
    iframe.onload = () => {
      const doc = iframe.contentDocument,
        root = doc.documentElement,
        html = this.deserialization(this.dom); //ååºåˆ—åŒ–
      //æ ¹å…ƒç´ å±žæ€§é™„åŠ 
      for (const { name, value } of Array.from(html.attributes)) {
        root.setAttribute(name, value);
      }
      root.removeChild(root.firstElementChild); //ç§»é™¤head
      root.removeChild(root.firstElementChild); //ç§»é™¤body
      Array.from(html.children).forEach((child) => {
        root.appendChild(child);
      });
      //åŠ ä¸ªå®šæ—¶å™¨åªæ˜¯ä¸ºäº†æŸ¥çœ‹æ–¹ä¾¿
      setTimeout(() => {
        this.replay();
      }, 2000);
    };
    document.body.appendChild(iframe);
  },
};

export default JSVideo;
